import React from 'react';
import PropTypes from 'prop-types';
import { Row, Col, Container } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { getIconForSoftSkill } from '../../mock/aboutMe/softSkillsData';
import '../../style/pages/_softSkills.scss';

const SoftSkills = ({ softSkills }) => {
  const columnWidth = '33.33%';

  return (
    <Container fluid className="softskills-page-container">
      <Row className="softskills-row">
        {Object.keys(softSkills).map((category) => (
          <Col lg={4} style={{ width: columnWidth }} key={category}>
            <div className="d-flex flex-column h-100">
              <div className="softskills-card">
                <h2 className="softskills-category-heading">{category}</h2>
                {softSkills[category].map((softskill) => (
                  <div className="softskills-item" key={softskill}>
                    <FontAwesomeIcon icon={getIconForSoftSkill(softskill)} className="icon" />
                    <span className="softskills-text">{softskill}</span>
                  </div>
                ))}
              </div>
            </div>
          </Col>
        ))}
      </Row>
    </Container>
  );
};

SoftSkills.propTypes = {
  softSkills: PropTypes.objectOf(PropTypes.arrayOf(PropTypes.string)).isRequired,
};

export default SoftSkills;
