import {
  faCode,
  faTasks,
  faGlobe,
  faHourglass,
  faHandshake,
  faComments,
  faClock,
  faChalkboardTeacher,
  faBullseye,
  faCommentsDollar,
  faUserFriends,
  faBullhorn,
  faSmile,
  faMedal,
  faComment,
  faRocket,
} from '@fortawesome/free-solid-svg-icons';

export const softSkills = {
  'Communication Skills': [
    'Active Listening',
    'Assertive Communication',
    'Effective Presentation Skills',
  ],
  'Problem Solving': ['Conflict Resolution', 'Solution-Oriented', 'Emotional Intelligence'],
  'Time Management and Work Ethic': ['Time Management', 'Work Ethic', 'Concentration'],
  'Personal Development': [
    'Growth Mindset',
    'Self-Confidence',
    'Self-Motivation',
    'Self-Awareness',
  ],
  'Team Collaboration': ['Agile Methodology (Scrum)', 'Constructive Feedback'],
  'Business Skills': ['Business Acumen'],
};

export const getIconForSoftSkill = (softSkill) => {
  const softSkillMap = {
    'Active Listening': faHandshake,
    'Assertive Communication': faTasks,
    'Effective Presentation Skills': faMedal,
    'Growth Mindset': faComments,
    'Self-Confidence': faBullhorn,
    'Self-Motivation': faRocket,
    'Self-Awareness': faHourglass,
    'Conflict Resolution': faClock,
    'Solution-Oriented': faSmile,
    'Emotional Intelligence': faComment,
    'Time Management': faChalkboardTeacher,
    'Work Ethic': faCode,
    Concentration: faCommentsDollar,
    'Agile Methodology (Scrum)': faBullseye,
    'Constructive Feedback': faUserFriends,
    'Business Acumen': faGlobe,
  };

  return softSkillMap[softSkill] || faCode;
};
